<style scoped>
.layout,.ivu-layout {
    height: 100%;
} 
.ivu-layout {
    background: #334054;
    background-size: 100%; 
}
.bgC-53d192 {
    background-color: #53d192;
    border: 0;
    outline: none;
}
.login {width:20%;min-width:200px;margin: 104px auto 0 auto;}
h2 {margin-bottom:30px;text-align:center;font-size: 30px;color: #fff;}
</style>
<template>
    <div class="layout">
        <Layout>
            <Content class="login">
                <!-- <Form ref="formInline" :model="formInline" :rules="ruleInline"> -->
                <h2>悠鲜盒约管理平台</h2>
                <Form ref="formInline" :model="formInline">
                    <FormItem prop="user">
                        <Input type="text" v-model="formInline.user" placeholder="请输入管理员账号">
                            <Icon type="ios-person-outline" slot="prepend"></Icon>
                        </Input>
                    </FormItem>
                    <FormItem prop="password">
                        <Input type="password" v-model="formInline.password" placeholder="请输入密码">
                            <Icon type="ios-lock-outline" slot="prepend"></Icon>
                        </Input>
                    </FormItem>
                    <FormItem>
                        <Button type="primary" class="bgC-53d192" long @click="handleSubmit('formInline')">登录</Button>
                    </FormItem>
                </Form>
            </Content>
        </Layout>
    </div>
</template>
<script>
export default {
    data () {
        return {
            formInline: {
                user: '',
                password: ''
            },
            ruleInline: {
                user: [
                { required: true, message: '请输入管理员账号', trigger: 'blur' }
                ],
                password: [
                { required: true, message: '请输入密码.', trigger: 'blur' },
                { type: 'string', min: 6, message: '密码长度小于6位', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        handleSubmit(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!');
                    this.$router.push({ path: '/' })
                } else {
                    this.$Message.error('Fail!');
                }
            })
        }
    }
}
</script>



